@font-face {
	font-family: 'CorkiTuscan';
	src: url('../fonts/CorkiTuscan/Corki-Tuscan.eot');
	src: url('../fonts/CorkiTuscan/Corki-Tuscan.eot?#iefix') format('embedded-opentype'),
		url('../fonts/CorkiTuscan/Corki-Tuscan.woff2') format('woff2'),
		url('../fonts/CorkiTuscan/Corki-Tuscan.woff') format('woff'),
		url('../fonts/CorkiTuscan/Corki-Tuscan.ttf') format('truetype'),
		url('../fonts/CorkiTuscan/Corki-Tuscan.svg#Corki-Tuscan') format('svg');
	font-weight: normal;
	font-style: normal;
}

